<template>
    <v-container fluid ma-0 pa-0 full-height>
        <v-card>
            <v-container fluid ma-0 pa-0 full-height>
                <h2 class="text-md-center mt-5">{{radios}}</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs9>
                        <v-btn v-if="radios=='Buku Kas'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
