                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Data {{radios}}
                        </v-btn>
                        <v-btn v-if="radios=='Buku Bank'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="tambahBtn">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Data {{radios}}
                        </v-btn>
                        <v-btn v-if="radios=='Buku Jurnal'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="tambahBtn">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Data {{radios}}
                        </v-btn>
                        <v-btn v-if="listKB && (radios=='Buku Kas' || radios=='Buku Bank')" depressed dark rounded style="text-transform: none !important;"
                            class="primary mx-2" :to="'/reportpembukuan/'+listKB" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Report
                        </v-btn>
                        <v-btn v-if="radios=='Buku Jurnal'" depressed dark rounded style="text-transform: none !important;"
                            class="primary mx-2" :to="'/reportjurnal'" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Report {{radios}}
                        </v-btn>
                        <v-btn v-if="radios=='Buku Jurnal'" @click="getHPembelian()" depressed dark rounded style="text-transform: none !important;"
                            color="indigo darken-4" class="primary mx-2">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Get Data
                        </v-btn>

                        <v-btn v-if="trigger_insert" @click="postingBtn() " depressed dark rounded style="text-transform: none !important;"
                            color="indigo darken-4" class="primary mx-2">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Posting Data
                        </v-btn>

                        <v-col cols="2" v-if="radios=='Buku Kas'">
                            <v-autocomplete class="mt-1" dense single-line outlined :items="listkb"
                                item-text="JenisPerkiraan" item-value="KodeUnik" label="Pilih Report*" v-model="listKB">
                            </v-autocomplete>
                        </v-col>
                        <v-col cols="2" v-if="radios=='Buku Bank'">
                            <v-autocomplete class="mt-1" dense single-line outlined :items="listkb"
                                item-text="JenisPerkiraan" item-value="KodeUnik" label="Pilih Report*" v-model="listKB">
                            </v-autocomplete>
                        </v-col>

                        <v-row no-gutters class="d-flex mt-4" v-if="radios=='Buku Jurnal'">
                        <v-col cols="2" class="mr-4">
                            <v-menu ref="menu3" v-model="menu3" :close-on-content-click="false"
                                :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                    <v-text-field v-model="form3.startdate" label="Start Date" dense single-line
                                        outlined v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="form3.startdate" no-title scrollable>
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="menu3 = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.menu3.save(date)">OK</v-btn>
                                </v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-col cols="2" class="mr-4">
                            <v-menu ref="menu4" v-model="menu4" :close-on-content-click="false"
                                :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                    <v-text-field v-model="form3.enddate" label="End Date" dense single-line outlined
                                        v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="form3.enddate" no-title scrollable>
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="menu4 = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.menu4.save(date), trigger=true">OK</v-btn>
                                </v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-col cols="1">
                            <v-btn depressed dark rounded style="text-transform: none !important;" class="mr-4"
                                @click="getData(), trigger=false">
                                <v-icon size="18">mdi-magnify</v-icon>
                            </v-btn>
                        </v-col>
                        </v-row>

                        
                        
                        <v-row class="d-flex" no-gutters v-if="radios=='Buku Besar'">
                            <v-col cols="5" class="d-flex" v-if="radios=='Buku Besar'">
                                <v-text-field v-model="findKodeper" dense single-line outlined rounded class="mr-4"
                                    label="Cari Data . . . . ."></v-text-field>
                                <v-btn depressed dark rounded style="text-transform: none !important;" @click="getBuku()" class="mr-4">
                                    <v-icon size="18">mdi-magnify</v-icon>
                                </v-btn>
                                <v-btn v-if="statusButton && radios=='Buku Besar'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                                    :to="'/reportbukubesar/'+findKodeper+'/'+radios" link>
                                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                                    Report Data
                                </v-btn>
                            </v-col>
                            <v-col cols="7">
                            </v-col>
                            <v-col cols="2" class="mt-2">
                                <h4>Nomor Perkiraan</h4>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field v-model="listper.KodeUnik" dense single-line outlined readonly></v-text-field>
                            </v-col>
                            <v-col cols="2" class="ml-10 mt-2">
                                <h4>Nama Perkiraan</h4>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field v-model="listper.JenisPerkiraan" dense single-line outlined readonly class="mr-4"></v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                        </v-row>

                        <v-row no-gutters class="d-flex" v-if="radios=='Buku Besar Daerah'">
                            <v-col cols="5" class="d-flex" v-if="radios=='Buku Besar Daerah'">
                                <v-text-field v-model="findKodeper" dense single-line outlined rounded class="mr-4"
                                    label="Cari Data . . . . ."></v-text-field>
                                <v-btn depressed dark rounded style="text-transform: none !important;" @click="getBuku()" class="mr-4">
                                    <v-icon size="18">mdi-magnify</v-icon>
                                </v-btn>

                                <v-btn v-if="statusButton && radios=='Buku Besar Daerah'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                                    :to="'/reportbukubesar/'+findKodeper+'/'+radios" link>
                                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                                    Report Data
                                </v-btn>

                            </v-col>
                            <v-col cols="7">
                            </v-col>
                            <v-col cols="2" class="mt-2">
                                <h4>Nomor Perkiraan</h4>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field v-model="listper.KodeUnik" dense single-line outlined readonly></v-text-field>
                            </v-col>
                            <v-col cols="2" class="ml-10 mt-2">
                                <h4>Nama Perkiraan</h4>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field v-model="listper.JenisPerkiraan" dense single-line outlined readonly class="mr-4"></v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                        </v-row>

                        
                        <v-radio-group v-model="radios" @change="getData(), setHeaders()" row>
                            <v-radio label="Buku Kas" value="Buku Kas"></v-radio>
                            <v-radio label="Buku Bank" value="Buku Bank"></v-radio>
                            <v-radio label="Buku Jurnal" value="Buku Jurnal"></v-radio>
                            <v-radio label="Buku Besar" value="Buku Besar"></v-radio>
                            <v-radio label="Buku Besar Daerah" value="Buku Besar Daerah"></v-radio>
                        </v-radio-group>

                        

                              <!-- <v-col class="d-flex" cols="12" sm="6">
                                  <v-autocomplete :items="saldos" item-label="Pilih Saldo" 
                                  item-text="NamaSaldo" item-value="JumlahSaldo"
                                  v-model="form.saldo" dense solo></v-autocomplete>
                              </v-col>
                            <v-col cols="12" sm="6">
                                <v-text-field v-model="form.saldo" dense solo readonly></v-text-field>
                            </v-col> -->
                    </v-flex>
                    <v-flex xs3 class="text-right">
                        <v-col cols="12">
                            <v-text-field v-model="keyword" dense single-line outlined append-icon="mdi-search" label="Cari Data" hide-details>
                            </v-text-field>
                        </v-col>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="bukus" :search="keyword">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.id">

                                <!-- <td>{{ SplitInto3Part(item["kodedebet"], '###-##-####') }}</td>
                                <td>{{ SplitInto3Part(item["kodekredit"], '###-##-####') }}</td> -->
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["tanggal"] }} </td>
                                <td class="title" v-if="item.nobukti=='null'"></td>
                                <td class="title" v-else>{{ item["nobukti"]}}</td>
                                <td class="title">{{ item["keterangan"] }}</td>

                                <td class="title" v-if="radios=='Buku Kas'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Kas'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                               
                                <td class="title" v-if="radios=='Buku Bank'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Bank'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>

                                <td class="title" v-if="radios=='Buku Jurnal'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Jurnal'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>

                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">{{ item["kode"].slice(0,3) + '-' + item["kode"].slice(3,5) + '-' + item["kode"].slice(5,9) }}</td>
                                <!-- <td class="title" v-if="radios!='Buku Jurnal'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                                <td class="title" v-else>{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title"  v-if="radios!='Buku Jurnal'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-else>{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td> -->
                                <td class="title" v-if="radios=='Buku Kas'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Kas'">Rp {{ item["debet"] | currency}}</td>
                                
                                <td class="title" v-if="radios=='Buku Bank'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Bank'">Rp {{ item["debet"] | currency}}</td>

                                <td class="title" v-if="radios=='Buku Jurnal'">Rp {{ item["debet"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Jurnal'">Rp {{ item["kredit"] | currency}}</td>
                                
                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">Rp {{ item["debet"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title">
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click="deleteData(item)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>


        
        <v-dialog v-model="dialog" persistent max-width="1100" @keydown.esc="dialog = false">
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Data {{radios}}</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-2">Kode Perkiraan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-autocomplete dense single-line outlined :items="listper" item-text="KodeUnik"
                                    item-value="KodeUnik" label="Kode Perkiraan*" v-model="form2.Kas"></v-autocomplete>
                            </v-col>
                            <v-col cols="7"></v-col>
                            
                            <v-col cols="12" class="overflow-y-auto" style="max-height:420px;">
                                <v-row class="mr-2 scroll-y" v-for="(forms, index) in form" :key="index" no-gutters>
                                    
                                    <v-col cols="2">
                                        <h3 class="mt-2">Tanggal</h3>
                                    </v-col>
                                    <v-col cols="2" style="display: flex">
                                        <v-menu ref="menu" v-model="forms.menu" :close-on-content-click="false"
                                            :return-value.sync="date" transition="scale-transition">
                                            <template v-slot:activator="{ on }">
                                                <v-text-field dense single-line outlined v-model="forms.tanggal"
                                                    label="Tanggal" class=" mr-4" readonly v-on="on"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="forms.tanggal" no-title scrollable>
                                                <v-spacer></v-spacer>
                                                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                                <v-btn text color="primary" @click="$refs.menu[index].save(date)">OK
                                                </v-btn>
                                            </v-date-picker>
                                        </v-menu>
                                    </v-col>
                                    <v-col cols="1">
                                        <v-btn icon color="red" light @click="deleteIndex(index)">
                                            <v-icon>mdi-trash-can</v-icon>
                                        </v-btn>
                                    </v-col>

                                    <v-col cols="1">
                                        <h3 class="mt-2 mr-4 text-right">No Bukti</h3>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-text-field dense single-line outlined label="No Bukti*"
                                            v-model="forms.nobukti" required></v-text-field>
                                    </v-col>

                                    <v-col cols="2">
                                        <h3 class="mt-2 mr-4 text-right">Keterangan</h3>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-text-field dense single-line outlined label="Keterangan*"
                                            v-model="forms.keterangan" required>
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="2">
                                        <h3 class="mt-2 mr-2">Perkiraan Lawan</h3>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-autocomplete dense single-line outlined :items="listperkiraans"
                                            item-text="KodeUnik" item-value="KodeUnik" label="Perkiraan Lawan*"
                                            v-model="forms.kodeLawan"></v-autocomplete>
                                    </v-col>
                                    <v-col cols="1">
                                        <v-autocomplete dense single-line outlined :items="listdaerah"
                                            item-text="Nama Daerah" item-value="KodeDaerah" label="Daerah"
                                            v-model="forms.daerah"></v-autocomplete>
                                    </v-col>
                                    <v-col cols="1">
                                        <h3 class="mt-2 text-right mr-4">Debet</h3>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-text-field dense single-line outlined label="Jumlah Debet*"
                                            v-model="forms.debet" required>
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="1"></v-col>
                                    <v-col cols="1">
                                        <h3 class="mt-2">Kredit</h3>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-text-field dense single-line outlined label="Jumlah Kredit*"
                                            v-model="forms.kredit" required>
                                        </v-text-field>
                                    </v-col>
                                    <h2>-----------------------------------------------------------------------------------------------------------------------
                                    </h2>
                                </v-row>
                                <v-btn depressed dark rounded style="text-transform: none !important;"
                                    color="green accent-3" @click="addData">
                                    <v-icon size="18" class="mr-2">mdi-plus-thick</v-icon>
                                    Tambah Item
                                </v-btn>

                            </v-col>
                        </v-row>

                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <v-dialog v-model="dialog2" persistent max-width="950" @keydown.esc="dialog2 = false">
            <v-card>
                <v-card-title>
                    <span class="headline">Edit Data {{radios}}</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Tanggal</h3>
                            </v-col>
                                <v-col cols="2">
                                    <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                        :return-value.sync="date" transition="scale-transition" offset-y
                                        min-width="290px">
                                        <template v-slot:activator="{ on }">
                                            <v-text-field dense single-line outlined v-model="edit.tanggal"
                                                label="Tanggal" v-on="on"></v-text-field>
                                        </template>
                                        <v-date-picker v-model="edit.tanggal" no-title scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                            <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                        </v-date-picker>
                                    </v-menu>
                                </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 mr-4 text-right">No Bukti</h3>
                            </v-col>    
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="No Bukti*" v-model="edit.nobukti" required></v-text-field>
                            </v-col>

                            <v-col cols="2">
                                <h3 class="mt-2 mr-4 text-right">Keterangan</h3>
                            </v-col>                            
                            <v-col  cols="2">
                                <v-text-field dense single-line outlined label="Keterangan*" v-model="edit.keterangan" required>
                                </v-text-field>
                            </v-col>

                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Kode Perkiraan</h3>
                            </v-col>                            
                            <v-col  cols="3">
                                <v-autocomplete dense single-line outlined :items="listall" item-text="JenisPerkiraan" item-value="KodeUnik" 
                                label="Kode Perkiraan*" v-model="edit.kodedebet"></v-autocomplete>        
                            </v-col>
                            <v-col cols="3">
                                <h3 class="mt-2 text-right" >Perkiraan Lawan</h3>
                            </v-col>                            
                            <v-col class="mr-1" cols="3">
                                <v-autocomplete dense single-line outlined :items="listall" item-text="JenisPerkiraan" item-value="KodeUnik" 
                                label="Perkiraan Lawan*" v-model="edit.kodekredit"></v-autocomplete>                           
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Jumlah Debet</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Jumlah Debet*" v-model="edit.debet"
                                    required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2 text-right">Jumlah Kredit</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Jumlah Debet*" v-model="edit.kredit"
                                    required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="updateData()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialog3"  max-width="500px"  @keydown.esc="dialog3 = false">
            <v-card>
                <v-card-title>
                    <span class="headline" style="color:red">Warning!!!</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <h2>Anda Akan Melakukan Operasi Berikut:</h2>
                        <br>
                        <h3> 1. Menarik Data HPembelian</h3>
                        <br>
                        <h3> 2. Update Saldo Pajak</h3>
                        <br>
                        <h3> 3. Update Saldo Pembelian</h3>
                        <br>
                        <h3> 4. Update Saldo Hutang Dagang</h3>
                        <br>
                        <h3> 5. Update Saldo Hutang Pajak</h3>
                        <br>
                        <h3> 6. Backup Data Ke Database Baru</h3>
                        <br>
                        <h3> 7. Menghapus Data Database Sekarang</h3>
                    </v-container>
                    <big>Apakah Anda yakin?</big>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="dialog3=false, trigger_insert=false">Cancel</v-btn>
                        <v-btn color="blue darken-1" text @click="postingData()">Yes</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>

export default {
    data() {
        return {
            radios:'Buku Kas',
            date: new Date().toISOString().substr(0, 10),
            menu: false,
            menu3: false,
            menu4: false,
            trigger: false,
            trigger_insert: false,
            bukus: [],
            listperkiraans: [],
            listall: [],
            // listbank: [],
            // listlain: [],
            // listkas: [],
            listper: [],
            listkb: [],
            listdaerah: [],
            listKB: '',
            statusUpdate: true,
            dialog: false,
            dialog2: false,
            dialog3: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                    class: 'title'
                },
                {
                    text: 'Tanggal',
                    value: 'tanggal',
                    class: 'title'
                },
                {
                    text: 'No Bukti',
                    value: 'nobukti',
                    class: 'title'
                },
                {
                    text: 'Keterangan',
                    value: 'keterangan',
                    class: 'title'
                },
                {
                    text: 'Kode Perkiraan',
                    value: 'kodedebet',
                    class: 'title'
                },
                {
                    text: 'Kode Lawan',
                    value: 'kodekredit',
                    class: 'title'
                },
                {
                    text: 'Debet',
                    value: 'debet',
                    class: 'title'
                },
                {
                    text: 'Kredit',
                    value: 'kredit',
                    class: 'title'
                },
                {
                    text: 'Aksi',
                    class: 'title',
                    value: null
                },
            ],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            findKodeper: '',
            edit: {
                tanggal: '',
                nobukti: '',
                keterangan: '',
                kodekredit: '',
                kodedebet: '',
                debet: 0,
                kredit: 0
            },
            form3: {
                startdate: '',
                enddate: '',
            },

            form2: {
                Kas: '',
            },
            form: [{
                tanggal: '',
                nobukti: '',
                keterangan: '',
                Kas: '',
                kodeLawan: '',
                daerah: '00',
                debet: 0,
                kredit: 0,
                menu: false,
            }],
            listper: '',
            buku: new FormData,
            typeInput: 'new',
            errors: '',
            updatedId: '',
            statusButton: false,
        }
    },
    
    methods: {
        // getSaldo() {
        // var uri = this.$apiUrl + 'saldo'
        // this.$http.get(uri).then(response => {
        //     this.saldos = response.data
        //     })
        // },
        setHeaders(){
            if (this.radios=="Buku Bank" || this.radios=="Buku Kas" || this.radios=="Buku Jurnal") {
                this.headers = [{
                        text: 'No',
                        value: 'no',
                        class: 'title'
                    },
                    {
                        text: 'Tanggal',
                        value: 'tanggal',
                        class: 'title'
                    },
                    {
                        text: 'No Bukti',
                        value: 'nobukti',
                        class: 'title'
                    },
                    {
                        text: 'Keterangan',
                        value: 'keterangan',
                        class: 'title'
                    },
                    {
                        text: 'Kode Perkiraan',
                        value: 'kodedebet',
                        class: 'title'
                    },
                    {
                        text: 'Kode Lawan',
                        value: 'kodekredit',
                        class: 'title'
                    },
                    {
                        text: 'Debet',
                        value: 'debet',
                        class: 'title'
                    },
                    {
                        text: 'Kredit',
                        value: 'kredit',
                        class: 'title'
                    },
                    {
                        text: 'Aksi',
                        class: 'title',
                        value: null
                    },
                ]
            }
            else if (this.radios=="Buku Besar" || this.radios=="Buku Besar Daerah") {
                this.headers = [{
                        text: 'No',
                        value: 'no',
                        class: 'title'
                    },
                    {
                        text: 'Tanggal',
                        value: 'tanggal',
                        class: 'title'
                    },
                    {
                        text: 'No Bukti',
                        value: 'nobukti',
                        class: 'title'
                    },
                    {
                        text: 'Keterangan',
                        value: 'keterangan',
                        class: 'title'
                    },
                    {
                        text: 'Kode Lawan',
                        value: 'kodekredit',
                        class: 'title'
                    },
                    {
                        text: 'Debet',
                        value: 'debet',
                        class: 'title'
                    },
                    {
                        text: 'Kredit',
                        value: 'kredit',
                        class: 'title'
                    },
                    {
                        text: 'Aksi',
                        class: 'title',
                        value: null
                    },
                ]
            }
        },
        deleteIndex(index) {
            this.form.splice(index, 1);
        },

        addData() {
            this.form.push({
                tanggal: '',
                nobukti: '',
                keterangan: '',
                kodeLawan: '',
                debet: 0,
                kredit: 0,
                menu: false,
            })
        },

        postingBtn(){
            this.dialog3 = true
        },

        tambahBtn() {
            this.dialog = true;
            this.getList();
        },

        getBukuBank() {
            var uri = this.$apiUrl + 'buku_kas'
            this.$http.get(uri).then(response => {
                this.bukus = response.data
            })
        },
        
        getBuku() {
            if (this.radios == "Buku Besar") {
                var uri = this.$apiUrl + 'buku_besar/' + this.findKodeper
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                    this.statusButton = true
                })
            } else if (this.radios == "Buku Besar Daerah") {
                var uri = this.$apiUrl + 'buku_besar_daerah/' + this.findKodeper
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                    this.statusButton = true
                })
            }

            var uri2 = this.$apiUrl + 'listperkiraan/' + this.findKodeper
            this.$http.get(uri2 ).then(response => {
                this.listper = response.data[0]
            })

        },

        getData() {
            this.bukus = []
            if (this.radios == 'Buku Kas') {
                var uri = this.$apiUrl + 'buku_kas'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                })
            } else if (this.radios == 'Buku Bank') {
                var uri = this.$apiUrl + 'buku_bank'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                })
            } else if (this.radios == 'Buku Jurnal') {
                var uri = this.$apiUrl + 'bukujurnal'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data

                })
                if (this.trigger == true) {
                    var uri = this.$apiUrl + 'bukujurnal/' + this.form3.startdate + '/' + this.form3.enddate
                    this.$http.get(uri).then(response => {
                        this.bukus = response.data
                        this.resetForm3();
                    })
                }
            } 
        },

        getHPembelian(){
                var uri = this.$apiUrl + 'bukujurnal/gethpembelian'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                    this.trigger_insert = true
                })
        },

        postingData() {
                var uri = this.$apiUrl + 'bukujurnal/inserthpembelian'
                this.$http.get(uri).then(response => {
                    var uri2 = this.$apiUrl + 'bukujurnal/updatepajak'
                    this.$http.get(uri2).then(response => {
                        var uri3 = this.$apiUrl + 'bukujurnal/updatepembelian'
                        this.$http.get(uri3).then(response => {
                            var uri4 = this.$apiUrl + 'bukujurnal/updatehutangdagang'
                            this.$http.get(uri4).then(response => {
                                var uri5 = this.$apiUrl + 'bukujurnal/updatehutangpajak'
                                this.$http.get(uri5).then(response => {
                                    var uri6 = this.$apiUrl + 'bukujurnal/backupdata'
                                    this.$http.get(uri6).then(response => {
                                        var uri7 = this.$apiUrl + 'bukujurnal/deletedata'
                                        this.$http.get(uri7).response
                                    })
                                })
                            })
                        })
                    })
                })
                this.getData();
                this.dialog3 = false
                this.trigger_insert = false
        },

        getListKB(){
            var uri = this.$apiUrl + 'listkasbank'
            this.$http.get(uri).then(response =>{
                this.listkb = response.data
            })
        },
        getListDaerah(){
            var uri = this.$apiUrl + 'daerah'
            this.$http.get(uri).then(response =>{
                this.listdaerah = response.data
            })
        },
        getList(){
            if (this.radios=='Buku Kas') {
                var uri = this.$apiUrl + 'kas'
                this.$http.get(uri).then(response => {
                    this.listper = response.data
                })
                var uri2 = this.$apiUrl + 'listlain'
                this.$http.get(uri2).then(response => {
                    this.listperkiraans = response.data
                })
            } else if (this.radios=='Buku Bank') {
                var uri = this.$apiUrl + 'bank'
                this.$http.get(uri).then(response => {
                    this.listper = response.data
                })
                var uri2 = this.$apiUrl + 'listlain'
                this.$http.get(uri2).then(response => {
                    this.listperkiraans = response.data
                })
            } else if (this.radios=='Buku Jurnal'){
                var uri = this.$apiUrl + 'listlain'
                this.$http.get(uri).then(response => {
                    this.listper = response.data
                })
                var uri2 = this.$apiUrl + 'listlain'
                this.$http.get(uri2).then(response => {
                    this.listperkiraans = response.data
                })
                var uri3 = this.$apiUrl + 'listperkiraan'
                this.$http.get(uri3).then(response => {
                    this.listall = response.data
                })
            }
            // console.log(this.listper)
        },
        Activity(key) {
            // untuk memposting aktivitas user yang login
            var activity = new FormData
            activity.append('activity', key)
            var uri = this.$apiUrl + 'activity'
            this.$http.post(uri, activity).then(response => {}).catch(error => {
                this.errors = error
            })
        },
        sendData() {
            var i;
            for (i = 0; i < this.form.length; i++) {
                this.buku = new FormData
                this.buku.append('tanggal', this.form[i].tanggal);
                this.buku.append('nobukti', this.form[i].nobukti);
                this.buku.append('keterangan', this.form[i].keterangan);
                // this.buku.append('kodedebet', this.form2.Kas);
                // this.buku.append('kodekredit', this.form[i].kodeLawan);
                this.form[i].kodeLawan = this.form[i].kodeLawan.slice(0,3) + this.form[i].daerah + this.form[i].kodeLawan.slice(5,9);
                if (this.radios == 'Buku Kas') {
                    
                    if (this.form[i].kredit === 0) {
                        this.buku.append('kodedebet', this.form2.Kas);
                        this.buku.append('kodekredit', this.form[i].kodeLawan);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    } else {
                        this.buku.append('kodedebet', this.form[i].kodeLawan);
                        this.buku.append('kodekredit', this.form2.Kas);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    }
                } else if (this.radios == 'Buku Bank') {
                    if (this.form[i].kredit === 0) {
                        this.buku.append('kodedebet', this.form2.Kas);
                        this.buku.append('kodekredit', this.form[i].kodeLawan);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    } else {
                        this.buku.append('kodedebet', this.form[i].kodeLawan);
                        this.buku.append('kodekredit', this.form2.Kas);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    }
                } else {
                    if (this.form[i].kredit === 0) {
                        this.buku.append('kodedebet', this.form2.Kas);
                        this.buku.append('kodekredit', this.form[i].kodeLawan);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    } else {
                        this.buku.append('kodedebet', this.form[i].kodeLawan);
                        this.buku.append('kodekredit', this.form2.Kas);
                        this.buku.append('debet', this.form[i].debet);
                        this.buku.append('kredit', this.form[i].kredit);
                        
                    }

                }
                var uri = this.$apiUrl + 'bukujurnal'
                this.load = true
                this.$http.post(uri, this.buku).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.getData(); //mengambil data user
                    this.resetForm();
                    this.Activity('Melakukan Create Buku Kas');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                })
            
            }
        
      },
      updateData() {
          this.buku.append('tanggal', this.edit.tanggal);
          this.buku.append('nobukti', this.edit.nobukti);
          this.buku.append('keterangan', this.edit.keterangan);
          this.buku.append('kodedebet', this.edit.kodedebet);
          this.buku.append('kodekredit', this.edit.kodekredit);
          this.buku.append('debet', this.edit.debet);
          this.buku.append('kredit', this.edit.kredit);
          var uri = this.$apiUrl + 'bukujurnal/' + this.updatedId;
          this.load = true
          this.$http.post(uri, this.buku).then(response => {
              this.snackbar = true; //mengaktifkan snackbar
              this.color = 'green'; //memberi warna snackbar
              this.text = response.data; //memasukkan pesan ke snackba
              this.load = false;
              this.dialog2 = false
              this.statusUpdate = true
              this.getData(); //mengambil data user
              this.resetForm2();
              this.Activity('Melakukan Update Buku Jurnal');
              this.typeInput = 'new';
          }).catch(error => {
              this.errors = error
              this.snackbar = true;
              this.text = 'Try Again';
              this.color = 'red';
              this.load = false;
              this.typeInput = 'new';
          })
      },
      editHandler(item) {
              this.getList();
              this.dialog2 = true;
              this.statusUpdate = false;
              this.edit.tanggal = item["tanggal"];
              this.edit.nobukti = item["nobukti"];
              this.edit.keterangan = item["keterangan"];
              this.edit.kodedebet = item["kodedebet"];
              this.edit.kodekredit = item["kodekredit"];
              this.edit.debet = item["debet"];
              this.edit.kredit = item["kredit"];
              this.updatedId = item["id"]
          },
        deleteData(item) { //mengahapus data
            var uri = this.$apiUrl +
                'bukujurnal/' + item.id; //data dihapus berdasarkan id
            this.$http.delete(uri).then(response => {
                this.snackbar = true;
                this.text = response.data;
                this.color = 'green'
                this.deleteDialog = false;
                this.getData();
                this.getSaldo();
                this.Activity('Melakukan Delete Buku Jurnal');
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
            })
        },
        setForm() {
            if (this.typeInput === 'new') {
                this.sendData()
            } else {
                this.updateData()
            }
        },
        close() {
            this.dialog = false
            this.dialog2 = false
            this.resetForm()
        },
        resetForm() {
            this.form = {
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    Kas: '',
                    kodeLawan: '',
                    jumlah: 0,
                },
                this.form = [{
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    Kas: '',
                    kodeLawan: '',
                    daerah: '00',
                    debet: 0,
                    kredit: 0,
                    menu: false,
                }],
                this.statusUpdate = true
        },
        resetForm2() {
            this.edit = {
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    kodedebet: '',
                    kodekredit: '',
                    jumlah: 0,
                },

                this.statusUpdate = true
        },
        resetForm3(){
            this.form3 = {
                startdate: '',
                enddate: '',
            }
            this.trigger=false
        }

        // SplitInto3Part(value, pattern) {
        //     var y = [],
        //         i = 0,
        //         x = value.toString();
        //     y.push(pattern.replace(/#/g, _ => x[i++]));
        //     return y;
        // }

    },
    mounted(){
        this.getBukuBank();
        this.getListDaerah();
        this.getListKB();
    },
}
</script>